h1, h2, h3, h4, h5, h6, p, blockquote, ul, ol, dl, dt, dd, figure, figcaption, div, table, tr, td, th, form, label, input, select, option, textarea, button {
  color: #0A2239;
  color: var(--tertiary);
}


